* {
  box-sizing: border-box;
  padding: 0;
}

@font-face {
  font-family: Avenir;
  font-weight: 300;
  src: url("Avenir-Book.657c3568.ttf");
}

@font-face {
  font-family: Avenir;
  font-weight: bold;
  src: url("Avenir-Heavy.01d9ab74.ttf");
}

body {
  font-family: Avenir, sans-serif;
  font-size: 16px;
}

.form {
  justify-content: center;
  margin: 20px auto 0;
  display: flex;
}

.form__big {
  width: 100%;
  height: 70px;
}

.form__label {
  width: 100%;
  display: block;
  position: relative;
}

.form__label:after {
  content: "";
  display: block;
  position: absolute;
}

.form__label_big:after {
  background-image: url("Search.ccd3c07b.svg");
  background-repeat: no-repeat;
  background-size: contain;
  width: 19px;
  height: 19px;
  top: 25px;
  left: 26px;
}

.form__input_big {
  padding-left: 62px;
  font-size: 16px;
  line-height: 22px;
}

.form__input_big:hover {
  transition: all .777s;
  box-shadow: 0 3px 8px #3d4e6133;
}

.form__input_big:focus {
  box-shadow: none;
  text-shadow: 1px 4px 4px #00000040;
  background: #f6f6f7;
  border-radius: 3px;
  font-weight: bold;
  transition: all .777s;
}

.form__input {
  color: #3d4e61;
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  outline: none;
  width: 100%;
  height: 100%;
  font-family: Avenir, sans-serif;
  font-weight: 300;
