  transition: all .777s;
  box-shadow: 0 3px 8px #3d4e6133;
}

.form__input_small:focus {
  box-shadow: none;
  background: #f6f6f7;
  border-radius: 3px;
  font-weight: bold;
  transition: all .777s;
}

